// src/app/LayoutClient.tsx
"use client";

import React, { useEffect, useState } from "react";
import { usePathname } from "next/navigation";
import { SessionProvider } from "../lib/session";
import Navbar from "./components/navbar";
import AdBar from "./components/AdBar";
import Footer from "./components/Footer";

export default function LayoutClient({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname() || "";
  const [mounted, setMounted] = useState(false);
  useEffect(() => setMounted(true), []);

  // Currently not used, but keeping in case you want to conditionally render later
  const inAuth = pathname.startsWith("/auth") || pathname.startsWith("/authx");

  // Show navbar everywhere (including auth pages)
  const showNavbar = true;

  return (
    <SessionProvider>
      {mounted && showNavbar && <Navbar />}
      {children}
      <AdBar />
      <Footer />
    </SessionProvider>
  );
}
